import { PATCH, POST } from "../UrlConfig/MethodType";

export const automateInstituteTypeFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/institute/automate/add/${data.id}/institute/type?excel_file=${data?.excel_file}`,
      method: PATCH,
      body: data.bodyParameter,
    }),
  });
};

