export const automateInstitutePlateformListFunction = (builder) => {
  return builder.query({
    query: (data) => `/institute/automate/all/type/university/stream/query`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => response?.config,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const automateInstitutePlateformStreamListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/institute/automate/all/stream/query?page=${data.page}&limit=${data.limit}&search=${data.search}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.streams,
        };
      else
        return {
          page: arg?.page,
          list: response?.streams,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },

    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

